.contact-container {
  border: 2px solid purple;
  width: 100vw;
  padding: 5rem 0rem 5rem 0rem;
  display: flex;
  align-items: center;
}

.contact-form {
  background-color: $form-background;
  display: flex;
  width: 30%;
  // height: 50vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 2rem;
  // border-radius: 10px 10px 10px 10px;

  -webkit-box-shadow: -2px 2px 0px 0px rgba(0, 0, 0, 0.23);
  -moz-box-shadow: -2px 2px 0px 0px rgba(0, 0, 0, 0.23);
  box-shadow: -2px 2px 0px 0px rgba(0, 0, 0, 0.23);
}

.contact-form h2 {
  margin-bottom: 1rem;
  position: relative;
}

.contact-form h2::after {
  content: "";
  position: absolute;
  border: 1px solid $secondary-color;
  width: 100%;
  bottom: -2px;
  right: 0;
  opacity: 0.5;
}

.MuiOutlinedInput-notchedOutline {
  border-color: $form-input-outline !important;
  //   background-color: $form-input !important;
}
